.nav {
  color: black;
  text-transform: uppercase;

  &__list {
    display: flex;
    justify-content: center;
    list-style: none;

    @media screen and (max-width: $small-screen-breakpoint) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    padding: 7px 20px;
    margin: 0 22.5px;
    border-radius: 10px;

    &_active {
      background-color: $active-color;
    }
  }

  &__link {
    color: white;
    text-decoration: none;

    transition: color $hover-duration;

    &:hover {
      color: $active-color;
    }
  }

}
